import React, { useState } from "react";
import { postData } from "../../redux/ActionCreators";
import { useDispatch } from "react-redux";
function AddStaffForm(props) {
  const dispatch = useDispatch();
  let [newStaff, setNewStaff] = useState({
    image: "/assets/images/alberto.png",
  });

  let staffList = [...props.staffs];
  let departmentList = [...props.departments];
  let [nameError, setNameError] = useState("");
  let [doBError, setDoBError] = useState("");
  let [startDateError, setStartDateError] = useState("");
  let [salaryScaleError, setSalaryScaleError] = useState("");
  let [annualLeaveError, setAnnualLeaveError] = useState("");
  let [overTimeError, setOverTimeError] = useState("");
  let [departmentError, setDepartmentError] = useState("");
  let [isDisabled, setIsDisable] = useState(true);
  function SendAddedUser() {
    // e.preventdefault();
    if (isDisabled === true) {
      alert("Yu cu nhp y  thng tin nhn vin mi!");
    } else {
      // newStaff.id=staffList.length;
      setNewStaff(newStaff);
      postData(dispatch,newStaff);
      // staffList.push(newStaff);
      // props.getNewStaffList(staffList);
      // props.getNewDepartmentList(departmentList);
      alert("Thm thnh cng nhn vin: "+newStaff.name );
      window.location.reload();
    }
  }
  function isChange(e) {
    let name = e.target.name;
    let value = e.target.value;
    checkValid(name, value);
  }
  function checkValid(name, value) {
    setIsDisable(false);
    if (name === "name") {
  
      if ( value===""|| value.length < 4 || value.length > 30 ) {
        setIsDisable(true);
        setNameError("Yu cu nhp nhiu hn 3 k t v t hn 30 k t");
      } else {
        setNameError("");
        newStaff[name] = value;
      }
    }
    if (name === "doB") {
      if (value === "") {
        setIsDisable(true);
        setDoBError("Yu cu bt buc");
      } else {
        setDoBError("");
        newStaff[name] = value;
      }
    }
    if (name === "startDate") {
      if (value === "") {
        setIsDisable(true);
        setStartDateError("Yu cu bt buc");
      } else {
        setStartDateError("");
        newStaff[name] = value;
      }
    }
    if (name === "department") {
      if (value === "") {
        setIsDisable(true);
        setDepartmentError("Yu cu bt buc");
      } else {
        setDepartmentError("");
        departmentList[Number(value)].numberOfStaff++;
        newStaff['departmentId'] = props.departments[Number(value)].id;
      }
    }
    if (name === "salaryScale") {
      if (Number(value) && Number(value) >= 1 && Number(value) <= 3) {
        setSalaryScaleError("");
        newStaff[name] = Number(value);
      } else {
        setIsDisable(true);
        setSalaryScaleError("Yu cu nhp s t 1-3");
      }
    }
    if (name === "annualLeave") {
      if (Number(value)) {
        setAnnualLeaveError("");
        newStaff[name] = Number(value);
      } else {
        setIsDisable(true);
        setAnnualLeaveError("Yu cu nhp s");
      }
    }
    if (name === "overTime") {
      if (Number(value)) {
        setOverTimeError("");
        newStaff[name] = Number(value);
      } else {
        setIsDisable(true);
        setOverTimeError("Yu cu nhp s");
      }
    }
  }
  if (props.hienThiForm === true) {
    return (
      <div className="col">
        <div>
          <h5 className="tieuDeTrang text-center mt-2">Thm Nhn Vin</h5>
          <form onSubmit={SendAddedUser}>
           
            {/* _____________ _____________ADD name UserName _____________ _____________*/}
            <div className="form-group row">
              {/* <label className="col-sm-4 col-form-label">Tn</label> */}
              <div className="col-sm-12">
                <input
                  type="text"
                  className="form-control"
                  id="name"
                  name="name"
                  placeholder="Tn nhn vin.."
                  onBlur={(event) => isChange(event)}
                />
              </div>
              <span className="text-danger">{nameError}</span>
            </div>
            {/* _____________ _____________ADD doB ngy sinh _____________ _____________*/}
            <div className="form-group row">
              <label className="col-sm-4 col-form-label">Ngy sinh</label>
              <div className="col-sm-8">
                <input
                  type="date"
                  className="form-control"
                  id="doB"
                  name="doB"

                  onBlur={(event) => isChange(event)}
                />
              </div>
              <span className="text-danger">{doBError}</span>
            </div>
            {/* _____________ _____________ADD startDate Ngy vo cng ty _____________ _____________*/}
            <div className="form-group row">
              <label className="col-sm-4 col-form-label">
                Ngy vo cng ty
              </label>
              <div className="col-sm-8">
                <input
                  type="date"
                  className="form-control"
                  id="startDate"
                  name="startDate"
                  onBlur={(event) => isChange(event)}
                />
              </div>
              <span className="text-danger">{startDateError}</span>
            </div>
            {/* _____________ _____________ADD department phng ban _____________ _____________*/}
            <div className="form-group row">
              {/* <label className="col-sm-4 col-form-label">Phng ban</label> */}
              <div className="col-sm-12">
                <select
                  className="form-select form-control"
                  aria-label="Default select example"
                  name="department"
                  defaultValue={""}
                  onBlur={(event) => isChange(event)}
                >
                  <option value="" disabled>
                    Chn phng ban
                  </option>
                  <option value="0">Sale</option>
                  <option value="1">HR</option>
                  <option value="2">Marketing</option>
                  <option value="3">IT</option>
                  <option value="4">Finance</option>
                </select>
              </div>
              <span className=" text-danger">{departmentError}</span>
            </div>
            {/* _____________ _____________ADD salaryScale h s lng _____________ _____________*/}
            <div className="form-group row">
              {/* <label className="col-sm-4 col-form-label">H s lng</label> */}
              <div className="col-sm-12">
                <input
                  type="text"
                  className="form-control"
                  id="salaryScale"
                  name="salaryScale"
                  placeholder="H s lng.."
                  onBlur={(event) => isChange(event)}
                />
              </div>
              <span className="text-danger">{salaryScaleError}</span>
            </div>
            {/* _____________ _____________ADD annualLeave s ngy ngh cn li _____________ _____________*/}
            <div className="form-group row">
              {/* <label className="col-sm-4 col-form-label">
                      S ngy ngh cn li
                    </label> */}
              <div className="col-sm-12">
                <input
                  type="text"
                  className="form-control"
                  id="annualLeave"
                  name="annualLeave"
                  placeholder="S ngy ngh cn li.."
                  onBlur={(event) => isChange(event)}
                />
              </div>
              <span className="text-danger">{annualLeaveError}</span>
            </div>
            {/* _____________ _____________ADD overTime s ngy  lm thm _____________ _____________*/}
            <div className="form-group row">
              {/* <label className="col-sm-4 col-form-label">
                      S ngy  lm thm
                    </label> */}
              <div className="col-sm-12">
                <input
                  type="text"
                  className="form-control"
                  id="overTime"
                  name="overTime"
                  placeholder="S ngy  lm thm.."
                  onBlur={(event) => isChange(event)}
                />
              </div>
              <span className="text-danger">{overTimeError}</span>
            </div>
            <div className="text-center">
              <button
                type="button"
                className="btn btn-success"
                onClick={() => SendAddedUser()}
              >
                Thm Mi
              </button>
            </div>
          </form>
        </div>
      </div>
    );
  } else {
    return <div></div>;
  }
}

export default AddStaffForm;
